.b-video
{
    position:relative;
    overflow:hidden;

    video
    {
        opacity:0.99;
    }
    .background-border
    {
        display:block;
        position:absolute;
        width:0;
        height:0;

        .inner
        {
            position:absolute;
            top:0;
            left:0;
            width:1500px;
            transform:translateX(-50%) translateY(-50%);
        }
        &.top
        {
            top:15px;
            left:50%;
            transform:rotate(-180deg);
        }
        &.right
        {
            top:50%;
            right:15px;
            transform:rotate(-90deg);
        }
        &.bottom
        {
            right:50%;
            bottom:15px;
            transform:rotate(0deg);
        }
        &.left
        {
            bottom:50%;
            left:15px;
            transform:rotate(-270deg);
        }
        &.size-30
        {
            .inner
            {
                height:30px;
                background-image:url('../images/background-mask-30.png');
            }
            &.top
            {
                top:15px;
            }
            &.right
            {
                right:15px;
            }
            &.bottom
            {
                bottom:15px;
            }
            &.left
            {
                left:15px;
            }
        }
        &.size-20
        {
            .inner
            {
                height:20px;
                background-image:url('../images/background-mask-20.png');
            }
            &.top
            {
                top:10px;
            }
            &.right
            {
                right:10px;
            }
            &.bottom
            {
                bottom:10px;
            }
            &.left
            {
                left:10px;
            }
        }
        &.size-10
        {
            .inner
            {
                height:10px;
                background-image:url('../images/background-mask-10.png');
            }
            &.top
            {
                top:5px;
            }
            &.right
            {
                right:5px;
            }
            &.bottom
            {
                bottom:5px;
            }
            &.left
            {
                left:5px;
            }
        }
        &.size-5
        {
            .inner
            {
                height:5px;
                background-image:url('../images/background-mask-5.png');
            }
            &.top
            {
                top:2.5px;
            }
            &.right
            {
                right:2.5px;
            }
            &.bottom
            {
                bottom:2.5px;
            }
            &.left
            {
                left:2.5px;
            }
        }
    }
}
