nav.navigation
{
    display:flex;
    flex-direction:column;
    justify-content:center;
    position:fixed;
    top:calc(50% - 120px);
    bottom:calc(50% - 120px);
    left:15px;
    width:40px;
    z-index:3;
    opacity:0;
    pointer-events:none;
    transition:opacity 0.3s, transform 0.3s;
    will-change:opacity, transform;

    a
    {
        display:block;
        text-align:center;
        text-decoration:none;

        &.up,
        &.down
        {
            position:relative;
            height:30px;
            opacity:0;
            pointer-events:none;
            transition:opacity 0.15s;
            will-change:opacity;

            .arrow
            {
                position:absolute;
                top:50%;
                left:50%;
                width:11px;
                height:22px;
                margin:-11px 0 0 -5px;
                transition:transform 0.15s;
                will-change:transform;

                svg *
                {
                    fill:#818181;
                }
            }
            &.up
            {

            }
            &.down
            {
                transform:rotate(180deg);
            }
            &.active
            {
                opacity:1;
                pointer-events:initial;
            }
            @mixin no-touch
            {
                &:hover
                {
                    .arrow
                    {
                        transform:translateY(-6px);
                    }
                }
            }
        }
        &.page
        {
            padding:7px 0 8px 0;

            .box
            {
                position:relative;
                display:block;
                height:1px;
                width:23px;
                margin:0 auto;
                border-right:1px solid #818181;
                border-bottom:1px solid #818181;
                border-left:1px solid #818181;
                overflow:hidden;
                opacity:0.3;
                transition:opacity 0.4s, height 0.4s;
                /* transition-delay:0.1s, 0.1s; */
                will-change:opacity, height;

                .stroke
                {
                    display:block;
                    position:absolute;
                    top:0;
                    right:0;
                    left:0;
                    height:1px;
                    background:#818181;
                    z-index:1;
                }
                .number
                {
                    display:block;
                    position:absolute;
                    top:50%;
                    width:100%;
                    margin:-6px 0 0 0;
                    font-family:'Mensch';
                    font-weight:400;
                    font-size:13px;
                    opacity:0;
                    color:#818181;
                    transition:opacity 0.4s;
                    will-change:opacity;
                }
                .text
                {
                    display:block;
                    position:absolute;
                    opacity:0;

                    /* 
                    top:50%;
                    left:50%;
                    margin-left:1px;
                    white-space:nowrap;
                    font-family:'Mensch';
                    font-weight:400;
                    font-size:13px;
                    color:#818181;
                    letter-spacing:1px;
                    transform:translateX(-50%) translateY(-50%) rotate(-90deg);
                    */
                   
                    top:50%;
                    width:21px;

                    transition:opacity 0.4s;
                    will-change:opacity;

                    &.latest-works
                    {
                        margin-top:-56px;
                        height:113px;
                        background:url('../images/navigation/latest-works.png');
                        background-size:21px 113px;
                    }
                    &.digital-experiences
                    {
                        margin-top:-77px;
                        height:154px;
                        background:url('../images/navigation/digital-experiences.png');
                        background-size:21px 154px;
                    }
                    &.content-production
                    {
                        margin-top:-77px;
                        height:154px;
                        background:url('../images/navigation/content-production.png');
                        background-size:21px 154px;
                    }
                    &.installations-and-retail
                    {
                        margin-top:-92px;
                        height:185px;
                        background:url('../images/navigation/installations-and-retail.png');
                        background-size:21px 185px;
                    }
                    &.works
                    {
                        margin-top:-33px;
                        height:66px;
                        background:url('../images/navigation/works.png');
                        background-size:21px 66px;
                    }
                }
            }
            /* &.active
            {
                .box
                {
                    height:42px;
                    opacity:1;
                    transition-delay:0s, 0s;

                    .number
                    {
                        opacity:1;
                        transition-delay:0.1s;
                    }
                }
            } */
            &.active,
            &.hover,
            &:hover
            {
                /* .box
                {
                    height:26px;
                    opacity:1;

                    .number
                    {
                        opacity:1;
                    }
                } */
                .box
                {
                    opacity:1;
                    
                    .number
                    {
                        opacity:0;
                    }
                    .text
                    {
                        opacity:1;
                        transition-delay:0.1s;
                    }
                }
            }
        }
    }
    &.active
    {
        opacity:1;
        pointer-events:initial;
    }
    @media only screen and (max-width:768px)
    {
        left:10px;
    }
}
body.is-mobile
{
    nav.navigation
    {
        display:none;
    }
}
