body.index_content_production
{
    .fullscreen-video
    {
        position:fixed;
        top:0;
        left:0;
        width:100%;
        height:100%;
        z-index:3;
        visibility:hidden;
        transition:visibility 0s;
        transition-delay:1.2s;

        .close
        {
            display:block;
            position:absolute;
            top:0;
            left:50%;
            width:40px;
            height:40px;
            margin:0 0 0 -25px;
            transform:translateY(-15px);
            opacity:0;
            transition:opacity 0.3s, transform 0.3s;
            transition-delay:0.0s, 0.0s;
            will-change:opacity, transform;

            .icon
            {
                display:block;
                position:absolute;
                top:50%;
                left:50%;
                width:22px;
                height:22px;
                margin:-11px 0 0 -11px;

                .bar
                {
                    display:block;
                    position:absolute;
                    top:0;
                    left:50%;
                    width:2px;
                    height:100%;
                    margin-left:-2px;
                    background:#000;
                    transition:transform 0.4s;
                    will-change:transform;

                    &.index-1
                    {
                        transform:rotate(45deg);
                        transition-delay:0.1s;
                    }
                    &.index-2
                    {
                        transform:rotate(135deg);
                    }
                }
            }
            @mixin no-touch
            {
                &:hover
                {
                    .icon
                    {
                        .bar
                        {
                            &.index-1
                            {
                                transform:rotate(135deg);
                            }
                            &.index-2
                            {
                                transform:rotate(225deg);
                            }
                        }
                    }
                }
            }
        }
        .video-container
        {
            position:absolute;
            top:40px;
            right:40px;
            bottom:40px;
            left:40px;
            background:#090909;
            opacity:0;
            transform-origin:100% 50%;
            transition:opacity 0.3s, transform 0.6s 0.3s cubic-bezier(0.85, 0, 0.15, 1);
            transition-delay:0.8s,0.3s;
            will-change:opacity, transform;

            .video
            {
                position:absolute;
                top:0;
                left:0;
                width:100%;
                height:100%;
            }
            .video,
            iframe
            {
                opacity:0;
                transition:opacity 0.3s;
                transition-delay:0s;
                will-change:opacity;
            }
        }
    }

    /* Fullscreen */
    header
    {
        &.remote-header
        {
            .logo
            {
                transition:opacity 0.3s, transform 0.3s;
                transition-delay:0.5s, 0.5s;
            }
            .open
            {
                transition:opacity 0.3s, transform 0.3s;
                transition-delay:0.6s, 0.6s;
            }
        }
    }
    nav.navigation
    {
        .inner
        {
            transition:opacity 0.3s, transform 0.3s;
            transition-delay:0.9s, 0.9s;
        }
    }
    section.split
    {
        .visuals
        {
            .play-circle
            {
                display:block;
                position:absolute;
                top:50%;
                left:50%;
                width:77px;
                height:77px;
                margin:-39px 0 0 -39px;
                border:2px solid #fff;
                border-radius:39px;

                .icon
                {
                    display:block;
                    position:absolute;
                    top:50%;
                    left:50%;
                    width:13px;
                    height:17px;
                    margin:-8px 0 0 -5px;

                    svg
                    {
                        display:block;
                        width:100%;
                        height:100%;

                        *
                        {
                            fill:#fff;
                        }
                    }
                }
            }
        }
        section.left
        {
            transition:opacity 0.3s, transform 0.3s;
            transition-delay:0.8s, 0.8s;
        }
        section.right
        {
            &.single-project
            {
                .texts
                {
                    transition:opacity 0.3s, transform 0.3s;
                    transition-delay:0.7s, 0.7s;
                    will-change:opacity, transform;
                }
                .sub-title
                {
                    display:block;
                    margin-top:6px;
                    margin-left:60px;
                    font-style:italic;
                    white-space: nowrap;

                    .arrow
                    {
                        display:inline-block;
                        width:20px;
                        height:13px;
                        margin:0 0 -2px 8px;
                        transition:transform 0.3s;
                        will-change:transform;

                        svg
                        {
                            display:block;
                            width:100%;
                            height:100%;
                        }
                    }
                }
            }
            &.multiple-projects
            {
                .texts
                {
                    bottom:80px;

                    .works
                    {
                        .work
                        {
                            margin-bottom:40px;

                            .sub-title
                            {
                                display:block;
                                margin-top:6px;
                                font-style:italic;

                                .arrow
                                {
                                    display:inline-block;
                                    width:20px;
                                    height:13px;
                                    margin:0 0 -2px 8px;
                                    transition:transform 0.3s;
                                    will-change:transform;

                                    svg
                                    {
                                        display:block;
                                        width:100%;
                                        height:100%;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    /* Fullscreen active */
    &.video-fullscreen-active
    {
        .fullscreen-video
        {
            visibility:visible;
            transition-delay:0s;

            .close
            {
                transform:translateY(0px);
                opacity:1;
                transition-delay:1.0s, 1.0s;
            }
            .video-container
            {
                opacity:1;
                transition-delay:0.2s, 0.5s;
                transition-timing-function:ease, cubic-bezier(0.75, 0, 0.5, 1);

                .video,
                iframe
                {
                    visibility:visible;
                    opacity:1;
                    transition-delay:1.2s;
                }
            }
        }
        header
        {
            &.remote-header
            {
                .logo
                {
                    opacity:0;
                    transform:translateX(-100px);
                    transition-delay:0.4s;
                    transition-timing-function:cubic-bezier(1, 0, 1, 1);
                }
                .open
                {
                    opacity:0;
                    transform:translateX(-100px);
                    transition-delay:0.3s;
                    transition-timing-function:cubic-bezier(1, 0, 1, 1);
                }
            }
        }
        nav.navigation
        {
            .inner
            {
                opacity:0;
                transform:translateX(-100px);
                transition-delay:0.2s;
                transition-timing-function:cubic-bezier(1, 0, 1, 1);
            }
        }
        section.split
        {
            section.left
            {
                opacity:0;
                transform:translateX(-100px);
                transition-delay:0.0s;
                transition-timing-function:cubic-bezier(1, 0, 1, 1);
            }
            section.right
            {
                &.single-project
                {
                    .texts
                    {
                        opacity:0;
                        transform:translateX(-100px);
                        transition-delay:0.1s;
                        transition-timing-function:cubic-bezier(1, 0, 1, 1);
                    }
                }
            }
        }
    }
}
