header.full-header
{
    position:fixed;
    top:0;
    left:0;
    height:100%;
    width:100%;
    z-index:4;
    overflow:hidden;
    pointer-events:none;

    .inner-1,
    .inner-2
    {
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
        overflow:hidden;
        background:#090909 url('../images/dark-background.jpg');
        transition:transform 1s;
        transition-timing-function:cubic-bezier(0.85, 0, 0.15, 1);
        will-change:transform;
    }
    .inner-1
    {
        transform:translateY(-100%);
    }
    .inner-2
    {
        transform:translateY(100%);
    }
    .background-video
    {
        position:absolute;
        top:50%;
        left:50%;
        width:1600px;
        height:400px;
        margin:-200px 0 0 -800px;
        opacity:0.5;

        video
        {
            width:100%;
            height:100%;
        }
    }
    .logo
    {
        display:block;
        position:absolute;
        top:30px;
        left:38px;
        width:70px;
        height:50px;

        .icon
        {
            position:absolute;
            display:block;
            left:12px;
            top:10px;
            width:47px;
            height:23px;

            svg *
            {
                fill:#818181;
            }
        }
    }
    .close
    {
        display:block;
        position:absolute;
        top:31px;
        left:50%;
        width:50px;
        height:50px;
        margin:0 0 0 -25px;

        .icon
        {
            display:block;
            position:absolute;
            top:50%;
            left:50%;
            width:27px;
            height:27px;
            margin:-14px 0 0 -14px;

            .bar
            {
                display:block;
                position:absolute;
                top:0;
                left:50%;
                width:3px;
                height:100%;
                margin-left:-2px;
                background:#fff;
                transition:transform 0.4s;

                &.index-1
                {
                    transform:rotate(45deg);
                    transition-delay:0.1s;
                }
                &.index-2
                {
                    transform:rotate(135deg);
                }
            }
        }
        @mixin no-touch
        {
            &:hover
            {
                .icon
                {
                    .bar
                    {
                        &.index-1
                        {
                            transform:rotate(135deg);
                        }
                        &.index-2
                        {
                            transform:rotate(225deg);
                        }
                    }
                }
            }
        }
    }
    .langs
    {
        display:block;
        position:absolute;
        left:50%;
        top:25%;
        width:300px;
        margin:-30px 0 0 -150px;
        text-align:center;

        .label
        {
            display:inline-block;
            vertical-align:middle;
            color:#797979;
            font-family:'Freight Big Pro';
            letter-spacing:1px;
            font-weight:300;
            font-style:italic;
            font-size:18px;
            line-height:35px;
            margin:0 7px 0 0;
        }
        a
        {
            display:inline-block;
            position:relative;
            top:1px;
            width:35px;
            height:35px;
            margin:0 5px;
            vertical-align:middle;
            border:2px solid #818181;
            border-radius:18px;
            text-decoration:none;
            line-height:32px;
            text-transform:uppercase;
            color:#818181;
            font-family:'Mohave';
            font-weight:600;
            font-size:13px;
            letter-spacing:1px;
            opacity:0.5;
            transition:opacity 0.15s;
            will-change:opacity;

            &.active
            {
                opacity:1;
            }
            @mixin no-touch
            {
                &:hover
                {
                    opacity:1;
                }
            }
        }
        .stroke
        {
            display:block;
            width:40px;
            height:1px;
            margin:13px auto 10px auto;
            background:#797979;
        }
    }
    .list
    {
        position:absolute;
        top:50%;
        left:50%;
        width:300px;
        height:300px;
        margin:-150px 0 0 -150px;
        text-align:center;
        font-size:55px;
        z-index:1;

        a
        {
            display:block;
            height:100px;
            line-height:100px;
            font-family:'Mohave';
            font-weight:600;
            font-style:normal;
            color:#fff;
            text-decoration:none;

            .name
            {
                position:relative;

                .stroke
                {
                    position:absolute;
                    top:50%;
                    left:-10px;
                    right:-10px;
                    height:5px;
                    margin:-2px 0 0 0;
                    background:#fff;
                    transform:scaleX(0);
                    transform-origin:0 0;
                    transition:transform 0.3s;
                    transition-timing-function:cubic-bezier(0.75, 0, 0.5, 1);
                }
            }
            @mixin no-touch
            {
                &:hover
                {
                    .name
                    {
                        .stroke
                        {
                            transform:scaleX(1);
                            transition-timing-function:cubic-bezier(0.25, 1.50, 0.75, 1);
                        }
                    }
                }
            }
        }
    }
    .social
    {
        display:block;
        position:absolute;
        left:50%;
        bottom:25%;
        width:160px;
        margin:0 0 -50px -80px;
        text-align:center;

        .label
        {
            color:#797979;
            font-family:'Freight Big Pro';
            letter-spacing:1px;
            font-weight:300;
            font-style:italic;
            font-size:18px;
        }
        .stroke
        {
            display:block;
            width:40px;
            height:1px;
            margin:13px auto 10px auto;
            background:#797979;
        }
        .buttons
        {
            font-size:0;

            a
            {
                position:relative;
                display:inline-block;
                width:45px;
                height:35px;

                span.icon
                {
                    display:block;
                    position:absolute;
                    top:50%;
                    left:50%;

                    svg
                    {
                        width:100%;
                        height:100%;

                        *
                        {
                            fill:#797979;
                            transition:fill 0.15s;
                        }
                    }
                }
                &.facebook
                {
                    span.icon
                    {
                        width:14px;
                        height:24px;
                        margin:-12px 0 0 -7px;
                    }
                }
                &.twitter
                {
                    span.icon
                    {
                        width:24px;
                        height:20px;
                        margin:-10px 0 0 -12px;
                    }
                }
                &.instagram
                {
                    span.icon
                    {
                        width:23px;
                        height:23px;
                        margin:-11px 0 0 -12px;
                    }
                }
                @mixin no-touch
                {
                    &:hover
                    {
                        span.icon
                        {
                            svg
                            {
                                *
                                {
                                    fill:#fff;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    &.active
    {
        pointer-events:initial;

        .inner-1
        {
            transform:translateY(0%);
        }
        .inner-2
        {
            transform:translateY(0%);
        }
    }
    @mixin max-width $breakpoint-xlarge
    {
        .background-video
        {
            width:1200px;
            height:300px;
            margin:-150px 0 0 -600px;
        }
    }
    @mixin max-width $breakpoint-large
    {
        .background-video
        {
            width:800px;
            height:200px;
            margin:-100px 0 0 -400px;
        }
    }
    @mixin max-width $breakpoint-small
    {
        .background-video
        {
            width:600px;
            height:150px;
            margin:-75px 0 0 -300px;
        }
    }
    @mixin max-width $breakpoint-xsmall
    {
        .background-video
        {
            width:400px;
            height:100px;
            margin:-50px 0 0 -200px;
        }
    }
    @mixin max-width $breakpoint-xxsmall
    {
        .logo
        {
            top:19px;
            left:18px;
        }
        .close
        {
            top:20px;
        }
        .background-video
        {
            width:300px;
            height:75px;
            margin:-32px 0 0 -150px;
        }
    }
    @mixin max-height $breakpoint-small
    {
        .langs
        {
            top:22%;
        }
        .social
        {
            bottom:20%;
        }
    }
    @mixin max-height $breakpoint-xsmall
    {
        .langs
        {
            top:22%;
        }
        .list
        {
            height:240px;
            margin-top:-120px;
            font-size:50px;

            a
            {
                height:80px;
                line-height:80px;

                .name
                {
                    .stroke
                    {
                        height:4px;
                    }
                }
            }
        }
        .social
        {
            bottom:16%;
        }
    }
    @mixin max-height $breakpoint-xxsmall
    {
        .logo
        {
            top:9px;
        }
        .close
        {
            top:10px;
        }
        .langs
        {
            .label
            {
                font-size:16px;
            }
        }
        .social
        {
            .label
            {
                font-size:16px;
            }
        }
        .list
        {
            height:180px;
            margin-top:-80px;
            font-size:34px;

            a
            {
                height:60px;
                line-height:60px;

                .name
                {
                    .stroke
                    {
                        height:4px;
                    }
                }
            }
        }
    }
    @media only screen and (max-height:$breakpoint-xsmall) and (orientation:landscape)
    {
        .langs
        {
            top:78%;
            bottom:auto;
            left:auto;
            right:50%;
            margin-top:-40px;
            padding-right:15px;
            text-align:right;

            .label
            {
                display:block;
                margin-bottom:8px;
            }
            .stroke
            {
                display:none;
            }
            a
            {
                text-align:center;
            }
        }
        .list
        {
            top:38%;
            left:0;
            height:60px;
            width:100%;
            margin-top:0;
            margin-left:0;

            a
            {
                display:inline-block;
                margin:0 15px;
            }
        }
        .social
        {
            top:78%;
            bottom:auto;
            left:50%;
            right:auto;
            margin-top:-40px;
            margin-left:0;
            padding-left:15px;
            text-align:left;

            .label
            {
                line-height:35px;
                margin-bottom:8px;
            }
            .stroke
            {
                display:none;
            }
            .buttons
            {
                margin-left:-15px;
            }
        }
    }
    @media only screen and (max-width:$breakpoint-xxsmall) and (orientation:landscape)
    {
        .langs
        {
            top:75%;
        }
        .social
        {
            top:75%;
        }
    }
}

header.remote-header
{
    position:relative;
    height:110px;
    z-index:3;
    overflow:hidden;

    .logo
    {
        display:block;
        position:absolute;
        top:50%;
        left:25px;
        width:100px;
        height:80px;
        margin-top:-43px;

        .logo-video
        {
            position:absolute;
            width:100px; /* 125 */
            height:64px; /* 80 */

            video
            {
                display:block;
                width:100%;
                height:100%;
                opacity:0;
                transition:opacity 0.5s;
                transition-delay:0.3s;
                will-change:opacity;
            }
            &.index-1
            {
                display:block;
                top:12px;
                left:-1px;
            }
            &.index-2
            {
                display:block;
                top:3px;
                left:-1px;
            }
            &.index-3
            {
                display:block;
                top:11px;
                left:-4px;
            }
            &.index-4
            {
                display:block;
                top:16px;
                left:-4px;
                width:106px;
                height:66px;
            }
            &.active
            {
                video
                {
                    opacity:1;
                }
            }
        }
        .icon
        {
            display:block;
            position:absolute;
            left:25px;
            top:28px;
            width:47px;
            height:23px;
            opacity:1;
            transition:opacity 0.5s;
            transition-delay:0.3s;
            will-change:opacity;

            svg *
            {
                fill:#606060;
            }
            /* &.active
            {
                opacity:1;
                transition-delay:0s;
            } */
        }
        /* &:after
        {
            position:absolute;
            top:50%;
            left:50%;
            content:'';
            display:block;
            width:47px;
            height:24px;
            margin:-12px 0 0 -24px;
            border:1px solid #f00;
        } */
    }
    .open
    {
        display:block;
        position:absolute;
        top:50%;
        left:50%;
        width:50px;
        height:50px;
        margin:-25px 0 0 -25px;

        .icon
        {
            display:block;
            position:absolute;
            top:50%;
            left:50%;
            width:25px;
            height:14px;
            margin:-7px 0 0 -12px;

            .bar
            {
                position:absolute;
                display:block;
                width:100%;
                height:2px;
                background:#000;
                transition:transform 0.2s cubic-bezier(0.75, 0, 0.5, 1);
                will-change:transform;

                &.index-1
                {

                }
                &.index-2
                {
                    top:50%;
                    margin-top:-1px;
                }
                &.index-3
                {
                    bottom:0;
                }
            }
        }
        @mixin no-touch
        {
            &:hover
            {
                .icon
                {
                    .bar
                    {
                        &.index-1
                        {
                            transform:translateX(-4px);
                        }
                        &.index-2
                        {
                            transform:translateX(4px);
                        }
                        &.index-3
                        {
                            transform:translateX(-4px);
                        }
                    }
                }
            }
        }
    }
    .controls
    {
        .button
        {
            display:block;
            position:absolute;
            top:50%;
            width:50px;
            height:50px;
            margin:-25px 0 0 -25px;
            opacity:0;
            pointer-events:none;
            transition:opacity 0.15s;
            will-change:opacity;

            .icon
            {
                position:absolute;
                top:50%;
                left:50%;
                transition:transform 0.3s;
                will-change:transform;
            }
            .text
            {
                position:absolute;
                bottom:-10px;
                left:50%;
                width:80px;
                margin-left:-40px;
                text-decoration:none;
                white-space:nowrap;
                font-family:'Mensch';
                font-size:12px;
                text-align:center;
                color:#000;
                pointer-events:none;
                opacity:0;
                transition:all 0.3s;
                will-change:opacity, transform;
            }
            &.prev
            {
                right:194px;

                .icon
                {
                    width:8px;
                    height:13px;
                    margin:-7px 0 0 -4px;
                    transform:rotate(180deg);
                }
                .text
                {
                    transform:translateX(15px);
                }
            }
            &.next
            {
                right:108px;

                .icon
                {
                    width:8px;
                    height:13px;
                    margin:-7px 0 0 -4px;
                }
                .text
                {
                    transform:translateX(-15px);
                }
            }
            &.close
            {
                right:22px;

                .icon
                {
                    width:13px;
                    height:13px;
                    margin:-7px 0 0 -7px;
                }
                .text
                {
                    transform:translateY(10px);
                }
            }
            &.active
            {
                opacity:1;
                pointer-events:initial;
            }
            @mixin no-touch
            {
                &:hover
                {
                    .text
                    {
                        opacity:1;
                        transform:translateX(0px);
                    }
                    &.prev
                    {
                        .icon
                        {
                            transform:rotate(180deg) translateX(6px);
                        }
                    }
                    &.next
                    {
                        .icon
                        {
                            transform:translateX(6px);
                        }
                    }
                    &.close
                    {
                        .icon
                        {
                            transform:rotate(90deg);
                        }
                    }
                }
            }
        }
    }
    @mixin max-width $breakpoint-small
    {
        .controls
        {
            .button
            {
                &.next
                {
                    right:92px;
                }
                &.prev
                {
                    right:162px;
                }
            }
        }
    }
    @mixin max-width $breakpoint-xsmall
    {
        .controls
        {
            .button
            {
                &.next
                {
                    right:72px;
                }
                &.prev
                {
                    right:122px;
                }
            }
        }
    }
    @mixin max-width $breakpoint-xxsmall
    {
        height:90px;

        .logo
        {
            left:5px;
        }
        .controls
        {
            .button
            {
                width:40px;

                &.close
                {
                    right:7px;
                }
                &.next
                {
                    right:47px;
                }
                &.prev
                {
                    right:87px;
                }
            }
        }
    }
    @mixin max-height $breakpoint-xxsmall
    {
        height:70px;
    }
}
