body.error_index
{
    section.content
    {
        position:absolute;
        top:50%;
        left:0;
        width:100%;
        margin:-190px 0 0 0;
        text-align:center;

        .code
        {
            margin:0 0 10px 0;
            font-size:250px;
            font-family:'Mohave';
            font-weight:600;
            color:#000000;
        }
        .main-title
        {
            margin:0 0 13px 0;
            font-family:'Mensch';
            font-weight:400;
            font-size:24px;
            text-transform:uppercase;
            letter-spacing:0.1em;
            color:#000000;
        }
        .sub-title
        {
            margin:0 0 80px 0;
            font-family:'Mensch';
            font-weight:400;
            font-size:16px;
            text-transform:uppercase;
            letter-spacing:0.1em;
            color:#898989;
        }
        a
        {
            display:inline-block;
            font-style:italic;
            text-decoration:none;

            .title
            {
                display:block;
                margin:0 0 13px 0;
            }
            .arrow
            {
                display:block;
                width:20px;
                height:13px;
                transition:transform 0.3s;
                will-change:transform;

                svg
                {
                    display:block;
                    width:100%;
                    height:100%;
                }
            }
            @mixin no-touch
            {
                &:hover
                {
                    .arrow
                    {
                        transform:translateX(6px);
                    }
                }
            }
        }
    }
}
