body.index_contact
{
    color:#060606;

    a
    {
        &.underline
        {
            &::after
            {
                background:#060606;
            }
        }
    }
    footer.footer
    {
        display:block;
    }
    .informations
    {
        margin:0 0 125px 0;
        text-align:center;

        .information
        {
            position:relative;
            display:inline-block;
            vertical-align:top;
            margin:0 58px;
            text-align:left;

            .title
            {
                position:relative;
                margin:0 0 10px 0;
                font-size:36px;

                .stroke
                {
                    display:block;
                    position:absolute;
                    top:18px;
                    left:-32px;
                    width:25px;
                    height:1px;
                    background:#000;
                }
            }
            .text
            {
                position:absolute;
                top:100%;
                left:0;
                width:180px;
                line-height:24px;

                a
                {
                    text-decoration:none;
                }
            }
            &.follow
            {
                .text
                {
                    width:220px;
                    a
                    {
                        margin:0 14px 0 0;
                    }
                }
            }
        }
    }
    .map
    {
        width:1080px;
        height:580px;
        margin:0 auto 145px auto;
        background:#e5e3df;
    }
    @mixin max-width $breakpoint-large
    {
        .map
        {
            width:800px;
            height:460px;
            margin-bottom:110px;
            background:#e5e3df;
        }
    }
    @mixin max-width $breakpoint-medium
    {
        .map
        {
            width:auto;
            margin-right:40px;
            margin-left:40px;
        }
    }
    @media only screen and (max-width:768px)
    {
        .informations
        {
            margin-bottom:55px;

            .information
            {
                display:block;
                margin-bottom:20px;

                .title
                {
                    margin-bottom:5px;
                }
                .text
                {
                    position:relative;
                    top:0;
                }
            }
        }
        .map
        {
            margin-bottom:55px;
        }
    }
    @mixin max-width $breakpoint-xsmall
    {
        
    }
    @mixin max-width $breakpoint-xxsmall
    {
        .informations
        {
            .information
            {
                .title
                {
                    font-size:30px;
                }
            }
        }
        .map
        {
            margin-left:20px;
            margin-right:20px;
        }
    }
    @media only screen and (orientation:portrait)
    {
        .map
        {
            height:calc(100vw * 400 / 600);
        }
    }
}
