section.split
{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    overflow:hidden;

    section.left
    {
        position:absolute;
        top:40px;
        right:50%;
        bottom:40px;
        left:40px;

        .video
        {
            position:absolute;
            top:50%;
            left:50%;
            width:478px;
            height:354px;
            margin:-177px 0 0 -239px;
        }
    }
    section.right
    {
        position:absolute;
        top:40px;
        right:40px;
        bottom:40px;
        left:50%;

        .visuals
        {
            display:block;
            position:absolute;
            top:0;
            right:0;
            bottom:0;
            left:160px;
            background:#cfd2d2;
            z-index:0;

            .picture
            {
                display:block;
                position:absolute;
                top:0;
                left:0;
                width:100%;
                height:100%;
            }
            .video-container
            {
                display:block;
                position:absolute;
                top:0;
                left:0;
                width:100%;
                height:100%;

                .video
                {
                    position:absolute;
                    opacity:0;
                    transition:opacity 0.3s;
                    will-change:opacity;

                    video
                    {
                        width:100%;
                        height:100%;
                    }
                    &.can-play
                    {
                        @mixin no-touch
                        {
                            &:hover
                            {
                                opacity:1;
                            }
                        }
                    }
                }
            }
            .visual
            {
                display:block;
                position:absolute;
                top:0;
                left:0;
                width:100%;
                height:100%;
                opacity:0;
                transition:opacity 0.3s;
                transition-delay:0.3s;
                will-change:opacity;

                &.active
                {
                    opacity:1;
                    transition-delay:0s;
                }
            }
        }
        &.single-project
        {
            .texts
            {
                position:absolute;
                top:calc(50% - 70px);
                width:160px;
                left:0;
                color:#000;

                .label
                {
                    position:absolute;
                    top:-70px;
                    left:42px;
                    font-family:'Mensch';
                    font-weight:400;
                    font-size:14px;

                    strong
                    {
                        font-weight:bold;
                        line-height:20px;
                    }
                }
                .title-container
                {
                    display:block;
                    margin:0 0 85px 0;
                    text-decoration:none;

                    .title
                    {
                        display:block;
                        font-size:65px;
                        width:400px;
                        margin:0 0 10px 60px;
                        font-family:'Mohave';
                        font-weight:600;
                        font-style:normal;
                    }
                    .arrow
                    {
                        display:block;
                        width:20px;
                        height:13px;
                        margin:0 0 0 64px;
                        transition:transform 0.3s;
                        will-change:transform;

                        svg
                        {
                            display:block;
                            width:100%;
                            height:100%;
                        }
                        &.play
                        {
                            position:relative;
                            left:-7px;
                        }
                    }
                    .date
                    {
                        position:absolute;
                        top:22px;
                        right:calc(100% - 13px);

                        .stroke
                        {
                            display:block;
                            position:absolute;
                            top:0;
                            right:0;
                            width:40px;
                            height:1px;
                            background:#000;
                            transform:scaleX(1);
                        }
                        .top
                        {
                            position:absolute;
                            right:0;
                            bottom:5px;
                        }
                        .bottom
                        {
                            position:absolute;
                            top:5px;
                            right:0;
                        }
                    }
                    @mixin no-touch
                    {
                        &:hover
                        {
                            .arrow
                            {
                                transform:translateX(6px);
                            }
                        }
                    }
                }
                .descriptions-container
                {
                    position:relative;
                    padding:11px 0 0 0;
                    line-height:18px;
                    font-family:'Mensch';
                    font-weight:400;
                    font-size:12px;

                    .stroke
                    {
                        display:block;
                        position:absolute;
                        top:0;
                        left:0;
                        width:25px;
                        height:1px;
                        background:#000;
                    }
                }
            }
        }
        &.multiple-projects
        {
            .texts
            {
                position:absolute;
                left:0;
                bottom:0;
                color:#000;
                z-index:1;

                .works
                {
                    position:absolute;
                    bottom:0;
                    left:0;
                    width:320px;

                    .view
                    {
                        display:block;
                        margin:0 0 180px 0;
                        padding:0 0 0 30px;
                        font-style:italic;
                        text-decoration:none;

                        .title
                        {
                            display:block;
                            margin:0 0 7px 0;
                        }
                        .arrow
                        {
                            display:block;
                            width:20px;
                            height:13px;
                            transition:transform 0.3s;
                            will-change:transform;

                            svg
                            {
                                display:block;
                                width:100%;
                                height:100%;
                            }
                        }
                        @mixin no-touch
                        {
                            &:hover
                            {
                                .arrow
                                {
                                    transform:translateX(6px);
                                }
                            }
                        }
                    }
                    .work
                    {
                        display:block;
                        position:relative;
                        margin:0 0 30px 0;
                        padding:0 0 0 30px;
                        text-decoration:none;
                        opacity:0.35;
                        transition:opacity 0.15s;
                        will-change:opacity;

                        .title
                        {
                            max-width:320px;
                            line-height:42px;
                            font-family:'Mohave';
                            font-weight:600;
                            font-style:normal;
                            font-size:42px;
                        }
                        .date
                        {
                            position:absolute;
                            top:20px;
                            right:100%;

                            .stroke
                            {
                                display:block;
                                position:absolute;
                                top:0;
                                right:0;
                                width:40px;
                                height:1px;
                                transform:scaleX(0);
                                transform-origin:100% 50%;
                                transition:transform 0.6s cubic-bezier(0.85, 0, 0.15, 1);
                                will-change:transform;

                                .inner
                                {
                                    display:block;
                                    position:absolute;
                                    top:0;
                                    left:0;
                                    width:100%;
                                    height:100%;
                                    background:#000;
                                    transform:scaleX(1);
                                    transform-origin:100% 50%;
                                    transition:transform 0.6s ease-out;
                                    will-change:transform;
                                }
                            }
                            .top
                            {
                                position:absolute;
                                right:0;
                                bottom:5px;
                                opacity:0;
                                transform:translateX(-30px);
                                transition:opacity 0.6s cubic-bezier(0.85, 0, 0.15, 1), transform 0.6s cubic-bezier(0.85, 0, 0.15, 1);
                                transition-delay:0.1s,0.1s;
                                will-change:opacity;
                            }
                            .bottom
                            {
                                position:absolute;
                                top:5px;
                                right:0;
                                opacity:0;
                                transform:translateX(-30px);
                                transition:opacity 0.6s cubic-bezier(0.85, 0, 0.15, 1), transform 0.6s cubic-bezier(0.85, 0, 0.15, 1);
                                transition-delay:0s,0s;
                                will-change:opacity;
                            }
                        }
                        &.active
                        {
                            opacity:1;

                            .stroke
                            {
                                transform:scaleX(1);
                            }
                            .date
                            {
                                .top
                                {
                                    opacity:1;
                                    transform:translateX(0px);
                                    transition-delay:0s,0s;
                                }
                                .bottom
                                {
                                    opacity:1;
                                    transform:translateX(0px);
                                    transition-delay:0.1s,0.1s;
                                }
                            }
                        }
                        @mixin no-touch
                        {
                            &:hover
                            {
                                opacity:1;

                                .stroke
                                {
                                    transform:scaleX(1);
                                }
                                .date
                                {
                                    .top
                                    {
                                        opacity:1;
                                        transform:translateX(0px);
                                        transition-delay:0s,0s;
                                    }
                                    .bottom
                                    {
                                        opacity:1;
                                        transform:translateX(0px);
                                        transition-delay:0.1s,0.1s;
                                    }
                                }
                            }
                        }
                    }
                    &.running
                    {
                        .work
                        {
                            &.active
                            {
                                .date
                                {
                                    .stroke
                                    {
                                        .inner
                                        {
                                            transform:scaleX(0);
                                            transition-duration:6s;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    @mixin max-width $breakpoint-xlarge
    {
        section.right
        {
            &.multiple-projects
            {
                .texts
                {
                    .works
                    {
                        .view
                        {
                            margin-bottom:80px;
                        }
                        .work
                        {
                            margin-bottom:22px;
                        }
                    }
                }
            }
        }
    }
    @mixin max-width $breakpoint-large
    {
        section.left
        {
            .video
            {
                width:360px;
                height:267px;
                margin-top:-133px;
                margin-left:-180px;

                video
                {
                    width:360px;
                    height:267px;
                }
            }
        }
        section.right
        {
            &.single-project
            {
                .texts
                {
                    .label
                    {
                        top:-50px;
                    }
                    .title-container
                    {
                        margin-bottom:65px;
                        margin-left:40px;

                        .title
                        {
                            font-size:55px;
                            margin-bottom:10px;
                        }
                        .arrow
                        {
                            margin-left:44px;
                        }
                    }
                }
            }
            &.multiple-projects
            {
                .texts
                {
                    .works
                    {
                        width:240px;
                        
                        .view
                        {
                            margin-bottom:60px;
                        }
                        .work
                        {
                            margin-bottom:16px;

                            .title
                            {
                                max-width:320px;
                                line-height:38px;
                                font-size:36px;
                            }
                        }
                    }
                }
            }
        }
    }
    @media screen and (max-aspect-ratio:3/2) and (orientation:landscape)
    {
        section.left
        {
            right:58%;
        }
        section.right
        {
            left:42%;
        }
    }
    @media only screen and (orientation:portrait)
    {
        section.left
        {
            right:40px;
            bottom:60%;
        }
        section.right
        {
            top:40%;
            left:40px;

            .visuals
            {
                left:42%;
            }
            &.single-project
            {
                .texts
                {
                    left:20%;
                }
            }
            &.multiple-projects
            {
                .texts
                {
                    left:20%;
                }
            }
        }
    }
}
