footer.footer
{
    display:none;
    position:absolute;
    right:0;
    bottom:0;
    left:0;
    height:150px;
    background:#fff;

    a
    {
        text-decoration:none;

        &.underline
        {
            &::after
            {
                background:#929292;
            }
        }
        &.sub-underline
        {
            .sub-line
            {
                &::after
                {
                    background:#929292;
                }
            }
        }
    }
    .credits
    {
        position:absolute;
        bottom:50%;
        left:40px;
        white-space:nowrap;
        font-size:14px;

        .stroke
        {
            vertical-align:middle;
            display:inline-block;
            width:20px;
            height:1px;
            margin:0 6px;
            background:#263b4c;
        }
    }
    .columns
    {
        text-align:right;

        .column
        {
            display:inline-block;
            vertical-align:top;
            padding:55px 82px 0 0;

            strong
            {
                font-family:'Mensch';
                font-weight:400;
                color:#9b9b9b;
            }
            &.logo
            {
                padding-top:35px;
                padding-right:52px;

                .logo
                {
                    display:inline-block;
                    padding:20px;

                    .icon
                    {
                        display:inline-block;
                        width:137px;
                        height:19px;

                        svg *
                        {
                            fill:#1a171b;
                        }
                    }
                }
            }
            &.text
            {
                font-size:14px;
                font-weight:300;
                line-height:18px;

                strong
                {
                    font-family:'Mensch';
                    font-weight:400;
                    color:#9b9b9b;
                }
            }
            &.social
            {
                padding-right:18px;

                .button
                {
                    position:relative;
                    display:inline-block;
                    width:36px;
                    height:50px;

                    .icon
                    {
                        position:absolute;
                        top:50%;
                        left:50%;

                        svg
                        {
                            width:100%;
                            height:100%;

                            *
                            {
                                fill:#bcbcbc;
                                transition:fill 0.15s;
                            }
                        }
                    }
                    &.facebook
                    {
                        .icon
                        {
                            width:14px;
                            height:24px;
                            margin:-12px 0 0 -7px;
                        }
                    }
                    &.twitter
                    {
                        .icon
                        {
                            width:24px;
                            height:20px;
                            margin:-10px 0 0 -12px;
                        }
                    }
                    &.instagram
                    {
                        .icon
                        {
                            width:24px;
                            height:24px;
                            margin:-12px 0 0 -12px;
                        }
                    }
                    &.scroll-up
                    {
                        .icon
                        {
                            width:11px;
                            height:22px;
                            margin:-11px 0 0 -5px;
                        }
                    }
                    @mixin no-touch
                    {
                        &:hover
                        {
                            .icon
                            {
                                svg
                                {
                                    *
                                    {
                                        fill:#444;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    @mixin max-width $breakpoint-xlarge
    {
        .columns
        {
            .column
            {
                padding-right:42px;

                &.logo
                {
                    padding-right:42px;
                }
            }
        }
    }
    @mixin max-width $breakpoint-large
    {
        .credits
        {
            .stroke
            {
                width:8px;
                margin-right:4px;
                margin-left:4px;
            }
            .links
            {
                /* display:block; */
            }
        }
        .columns
        {
            .column
            {
                padding-right:22px;

                &.logo
                {
                    padding-right:22px;
                }
            }
        }
    }
    @mixin max-width $breakpoint-medium
    {
        .credits
        {
            bottom:20px;
        }
        .columns
        {
            .column
            {
                padding-top:35px;

                &.logo
                {
                    padding-top:15px;
                }
            }
        }
    }
    @media only screen and (max-width:768px)
    {
        .columns
        {
            .column
            {
                &.social
                {
                    position:absolute;
                    bottom:0;
                    right:0;
                    padding-top:0;
                    padding-right:5px;
                }
            }
        }
    }
    @mixin max-width $breakpoint-xxsmall
    {
        height:auto;

        .credits
        {
            position:relative;
            top:0;
            left:0;
            padding:10px 10px 15px 20px;
        }
        .columns
        {
            .column
            {
                display:block;

                &.logo
                {
                    position:absolute;
                    top:0;
                    left:0;
                }
                &.social
                {
                    position:relative;
                    padding-top:10px;
                }
            }
        }
    }
}
