.main-loader
{
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:100%;
    z-index:11;
    overflow:hidden;
    pointer-events:none;

    .inner-1,
    .inner-2
    {
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
        overflow:hidden;
        background:#090909 url('../images/dark-background.jpg');
        transition:transform 1s;
        transition-timing-function:cubic-bezier(0.85, 0, 0.15, 1);
        will-change:transform;
    }
    .inner-1
    {
        transform:translateX(-100%);
    }
    .inner-2
    {
        transform:translateX(100%);
    }
    .background-video
    {
        position:absolute;
        top:50%;
        left:50%;
        width:1600px;
        height:400px;
        margin:-200px 0 0 -800px;
        opacity:1;

        video
        {
            width:100%;
            height:100%;
        }
    }
    &.active
    {
        pointer-events:initial;

        .inner-1
        {
            transform:translateX(0%);
        }
        .inner-2
        {
            transform:translateX(0%);
        }
    }
    @mixin max-width $breakpoint-xlarge
    {
        .background-video
        {
            width:1200px;
            height:300px;
            margin:-150px 0 0 -600px;
        }
    }
    @mixin max-width $breakpoint-large
    {
        .background-video
        {
            width:800px;
            height:200px;
            margin:-100px 0 0 -400px;
        }
    }
    @mixin max-width $breakpoint-small
    {
        .background-video
        {
            width:600px;
            height:150px;
            margin:-75px 0 0 -300px;
        }
    }
    @mixin max-width $breakpoint-xsmall
    {
        .background-video
        {
            width:400px;
            height:100px;
            margin:-50px 0 0 -200px;
        }
    }
    @mixin max-width $breakpoint-xxsmall
    {
        .background-video
        {
            width:300px;
            height:75px;
            margin:-32px 0 0 -150px;
        }
    }
}
