body.index_credits
{
    footer.footer
    {
        display:block;
    }
    section.content
    {
        width:1000px;
        margin:130px auto 130px auto;
        text-align:center;

        .title
        {
            margin:0 0 32px 0;
            font-size:100px;
            font-family:'Mohave';
            font-weight:600;
            color:#000000;
        }
        .sub-title
        {
            margin:0 0 100px 0;
            font-size:26px;
            line-height:30px;
            font-family:'Mohave';
            font-weight:600;
            color:#000000;
        }
        .text
        {
            width:800px;
            margin:0 auto;
            text-align:left;
            line-height:30px;

            p
            {
                margin:0 0 1.6em 0;
            }
            .title-1
            {
                font-size:22px;
                font-style:italic;
            }
        }
    }
    @mixin max-width $breakpoint-xlarge
    {
        section.content
        {
            margin-top:100px;
            margin-bottom:100px;

            .title
            {
                font-size:90px;
            }
            .sub-title
            {
                margin-bottom:80px;
                font-size:24px;
            }
        }
    }
    @mixin max-width $breakpoint-large
    {
        section.content
        {
            width:800px;
            margin-top:60px;
            margin-bottom:60px;

            .title
            {
                margin-bottom:26px;
                font-size:80px;
            }
            .sub-title
            {
                margin-bottom:70px;
                font-size:22px;
                line-height:26px;
            }
            .text
            {
                width:640px;
            }
        }
    }
    @mixin max-width $breakpoint-small
    {
        section.content
        {
            width:auto;
            margin-top:50px;
            margin-right:40px;
            margin-bottom:50px;
            margin-left:40px;

            .title
            {
                font-size:75px;
            }
            .sub-title
            {
                margin-bottom:50px;
                font-size:20px;
                line-height:24px;
            }
            .text
            {
                width:auto;
            }
        }
    }
    @mixin max-width $breakpoint-xsmall
    {
        section.content
        {
            margin-top:40px;
            margin-bottom:40px;

            .title
            {
                font-size:70px;
            }
            .sub-title
            {
                margin-bottom:40px;
                font-size:18px;

                br
                {
                    display:none;
                }
            }
        }
    }
    @mixin max-width $breakpoint-xxsmall
    {
        section.content
        {
            margin-top:30px;
            margin-right:20px;
            margin-bottom:30px;
            margin-left:20px;

            .title
            {
                font-size:60px;
            }
            .sub-title
            {
                font-size:16px;
            }
        }
    }
}
