body.works_index
{
    footer.footer
    {
        display:block;
    }
    .works
    {
        width:700px;
        margin:20px auto 180px;

        .work
        {
            display:block;
            position:relative;
            text-decoration:none;
            padding:100px 0;
            color:#000000;
            transition:transform 0.6s cubic-bezier(0.85, 0, 0.15, 1);
            will-change:transform;

            .inner
            {
                display:block;
                position:relative;
                transition:transform 0.6s cubic-bezier(0.85, 0, 0.15, 1);
                will-change:transform;
            }
            .picture-container
            {
                display:block;
                position:absolute;
                left:0;
                width:100%;
                height:155px;
                transform:scaleY(1);
                overflow:hidden;

                .picture
                {
                    position:absolute;
                    left:0;
                    width:100%;
                    height:310px;
                    transition:transform 0.6s cubic-bezier(0.85, 0, 0.15, 1);
                    will-change:transform;
                }
                .mask
                {
                    display:block;
                    position:absolute;
                    top:0;
                    left:0;
                    width:100%;
                    height:100%;
                    background:#ffffff;
                    opacity:0.65;
                }
                &.top
                {
                    bottom:50%;

                    .picture
                    {
                        top:0;
                        transform:translateY(50%);
                    }
                }
                &.bottom
                {
                    top:50%;

                    .picture
                    {
                        bottom:0;
                        transform:translateY(-50%);
                    }
                }
            }
            .date
            {
                display:block;
                position:absolute;
                top:24px;
                right:calc(100% - 100px);
                z-index:1;

                .stroke
                {
                    display:block;
                    position:absolute;
                    top:0;
                    right:0;
                    width:40px;
                    height:1px;
                    background:#000;
                    transform-origin:100% 50%;
                    transition:transform 0.6s cubic-bezier(0.85, 0, 0.15, 1);
                    transition-delay:0.3s;
                    will-change:transform;
                }
                .top
                {
                    position:absolute;
                    right:0;
                    bottom:5px;
                }
                .bottom
                {
                    position:absolute;
                    top:5px;
                    right:0;
                }
            }
            .title-container
            {
                display:block;
                position:relative;
                padding-left:125px;
                z-index:1;

                .title
                {
                    display:block;
                    line-height:65px;
                    font-family:'Mohave';
                    font-weight:600;
                    font-size:60px;
                }
                .sub-title
                {
                    display:block;
                    position:absolute;
                    top:110%;
                    left:125px;
                    margin-left:2px;
                    font-family:'Mensch';
                    font-weight:400;
                    font-size:14px;
                }
            }
            .infos
            {
                position:absolute;
                top:24px;
                right:calc(100% + 35px);
                z-index:1;
                pointer-events:none;

                .item
                {
                    opacity:0;
                    transform:translateX(-30px);
                    transition:opacity 0.6s cubic-bezier(0.85, 0, 0.15, 1), transform 0.6s cubic-bezier(0.85, 0, 0.15, 1);
                    will-change:opacity, transform;
                    white-space:nowrap;

                    &:nth-child(1) { transition-delay:0.0s, 0.0s; }
                    &:nth-child(2) { transition-delay:0.1s, 0.1s; }
                    &:nth-child(3) { transition-delay:0.2s, 0.2s; }
                    &:nth-child(4) { transition-delay:0.3s, 0.3s; }
                    &:nth-child(5) { transition-delay:0.4s, 0.4s; }
                    &:nth-child(6) { transition-delay:0.5s, 0.5s; }
                    &:nth-child(7) { transition-delay:0.6s, 0.6s; }
                    &:nth-child(8) { transition-delay:0.7s, 0.7s; }
                    &:nth-child(9) { transition-delay:0.8s, 0.8s; }
                }
                .top
                {
                    position:absolute;
                    right:0;
                    bottom:5px;
                    text-align:right;
                    font-family:'Mensch';
                    font-size:14px;
                    font-weight:700;
                }
                .bottom
                {
                    position:absolute;
                    top:5px;
                    right:0;
                    text-align:right;
                    font-family:'Mensch';
                    font-size:12px;
                    line-height:18px;
                }
            }
            .view
            {
                display:block;
                position:absolute;
                top:2px;
                left:calc(100% + 20px);
                width:100px;
                font-style:italic;
                text-decoration:none;
                z-index:1;
                pointer-events:none;
                opacity:0;
                transform:translateX(50px);
                transition:opacity 0.6s cubic-bezier(0.85, 0, 0.15, 1), transform 0.6s cubic-bezier(0.85, 0, 0.15, 1);
                transition-delay:0.0s;
                will-change:opacity, transform;

                .title
                {
                    display:block;
                    margin:0 0 7px 0;
                    white-space:nowrap;
                }
                .arrow
                {
                    display:block;
                    width:20px;
                    height:13px;
                    transition:transform 0.3s;
                    will-change:transform;

                    svg
                    {
                        display:block;
                        width:100%;
                        height:100%;
                    }
                }
            }
            /* &:hover, */
            &.active
            {
                .picture-container
                {
                    &.top
                    {
                        .picture
                        {
                            transform:translateY(0%);
                            transition-delay:0.0s;
                        }
                    }
                    &.bottom
                    {
                        .picture
                        {
                            transform:translateY(0%);
                            transition-delay:0.0s;
                        }
                    }
                }
                .date
                {
                    .stroke
                    {
                        transform:scaleX(4.05);
                        transition-delay:0s;
                    }
                }
                .infos
                {
                    pointer-events:initial;

                    .item
                    {
                        opacity:1;
                        transform:translateX(0px);

                        &:nth-child(1) { transition-delay:0.2s, 0.2s; }
                        &:nth-child(2) { transition-delay:0.3s, 0.3s; }
                        &:nth-child(3) { transition-delay:0.4s, 0.4s; }
                        &:nth-child(4) { transition-delay:0.5s, 0.5s; }
                        &:nth-child(5) { transition-delay:0.6s, 0.6s; }
                        &:nth-child(6) { transition-delay:0.7s, 0.7s; }
                        &:nth-child(7) { transition-delay:0.8s, 0.8s; }
                        &:nth-child(8) { transition-delay:0.9s, 0.9s; }
                        &:nth-child(9) { transition-delay:1.0s, 1.0s; }
                    }
                }
                .view
                {
                    pointer-events:initial;
                    opacity:1;
                    transform:translateX(0px);
                    transition-delay:0.2s;
                }
            }
            @for $i from 2 to 30
            {
                &:nth-child($i)
                {
                    transform:translateY(calc(-100 * ( $(i) - 1 ) * 1px));
                }
            }
            @mixin no-touch
            {
                &:hover
                {
                    .view
                    {
                        .arrow
                        {
                            transform:translateX(6px);
                        }
                    }
                }
            }
        }
        &.started
        {
            .work
            {
                transform:translateY(0px);
            }
        }
    }
