/* General */
html
{
    background:#f8f8f8;
}
body
{
    /*@mixin font-smoothing antialiased;*/
    font-family:'Freight Big Pro';
    font-weight:300;
    font-size:16px;
    color:#40484e;
    background:url(../images/background.jpg);
    overflow-x:hidden;
    min-height:100vh;

    @mixin max-width $breakpoint-xsmall
    {
        font-size:15px;
    }
    @mixin max-width $breakpoint-xxsmall
    {
        font-size:14px;
    }
}
.wrapper
{
    position:relative;
    min-height:100vh;
    padding-bottom:150px;

    @mixin max-width $breakpoint-xxsmall
    {
        padding-bottom:280px;
    }
}


/* Ajax loading */
.remote-header,
.navigation,
.ajax-container,
.footer
{
    transition:opacity 0.7s;
    /* will-change:opacity; */
}

body.ajax-transition
{
    .remote-header,
    .navigation,
    .ajax-container,
    .footer
    {
        opacity:0;
        transition-duration:0.7s;
    }
}

/* Page elements transition */
.page-element
{
    position:relative;
    transition-property:transform, opacity;
}
body.page-elements-arriving
{
    .page-element
    {
        transition-timing-function:cubic-bezier(0, 1, 1, 1);
    }
}
body.page-elements-leaving
{
    .page-element
    {
        transition-timing-function:cubic-bezier(1, 0, 1, 1);
    }
}
body.page-elements-animated
{
    .page-element
    {
        transition-duration:0.4s;

        &.delay-0  {transition-delay:0.00s;}
        &.delay-1  {transition-delay:0.05s;}
        &.delay-2  {transition-delay:0.10s;}
        &.delay-3  {transition-delay:0.15s;}
        &.delay-4  {transition-delay:0.20s;}
        &.delay-5  {transition-delay:0.25s;}
        &.delay-6  {transition-delay:0.30s;}
        &.delay-7  {transition-delay:0.35s;}
        &.delay-8  {transition-delay:0.40s;}
        &.delay-9  {transition-delay:0.45s;}
        &.delay-10 {transition-delay:0.50s;}
        &.delay-11 {transition-delay:0.55s;}
        &.delay-12 {transition-delay:0.60s;}
        &.delay-13 {transition-delay:0.65s;}
        &.delay-14 {transition-delay:0.70s;}
        &.delay-15 {transition-delay:0.75s;}
        &.delay-16 {transition-delay:0.80s;}
        &.delay-17 {transition-delay:0.85s;}
        &.delay-18 {transition-delay:0.90s;}
        &.delay-19 {transition-delay:0.95s;}
    }
}
body.page-elements-up
{
    .page-element
    {
        transform:translateY(-100px);
        opacity:0;

        &.perpendicular
        {
            transform:translateX(-100px);
            opacity:0;
        }
    }
}
body.page-elements-down
{
    .page-element
    {
        transform:translateY(100px);
        opacity:0;

        &.perpendicular
        {
            transform:translateX(100px);
            opacity:0;
        }
    }
}
body.page-elements-left
{
    .page-element
    {
        transform:translateX(-100px);
        opacity:0;

        &.perpendicular
        {
            transform:translateY(-100px);
            opacity:0;
        }
    }
}
body.page-elements-right
{
    .page-element
    {
        transform:translateX(100px);
        opacity:0;

        &.perpendicular
        {
            transform:translateY(100px);
            opacity:0;
        }
    }
}

/* Selection */
::-moz-selection {background:#888; }
::selection {background:#888; }

/* Clearfix */
.clearfix
{
    overflow:auto;zoom:1;
}

/* Scroll */
.b-scroll-set
{
    cursor:pointer;
}

/* Links */
a
{
    &.underline
    {
        position:relative;
        display:inline-block;
        text-decoration:none;

        &::after
        {
            content:'';
            display:block;
            position:absolute;
            right:0;
            bottom:0;
            left:0;
            height:1px;
            background:#263b4c;
            transform:scaleX(0);
            transform-origin:0% 50%;
            transition:transform 0.2s;
            transition-timing-function:cubic-bezier(0.75, 0, 0.5, 1);
            will-change:transform;
        }
        &:hover
        {
            &::after
            {
                transform:scaleX(1);
                transition-timing-function:cubic-bezier(0.25, 1.50, 0.75, 1);
            }
        }
    }
    &.sub-underline
    {
        .sub-line
        {
            position:relative;
            display:inline-block;

            &::after
            {
                content:'';
                display:block;
                position:absolute;
                right:0;
                bottom:0;
                left:0;
                height:1px;
                background:#263b4c;
                transform:scaleX(0);
                transform-origin:0% 50%;
                transition:transform 0.2s;
                transition-timing-function:cubic-bezier(0.75, 0, 0.5, 1);
                will-change:transform;
            }
        }
        &:hover
        {
            .sub-line
            {
                &::after
                {
                    transform:scaleX(1);
                    transition-timing-function:cubic-bezier(0.25, 1.50, 0.75, 1);
                }
            }
        }
    }
}

/* Loads */
.b-to-load
{
    img
    {
        opacity:0;
        transition:opacity 0.5s;
    }
    &.b-loaded
    {
        img
        {
            opacity:1;
        }
    }
}


/* Videos fallback */
.IIV::-webkit-media-controls-play-button,
video::-webkit-media-controls-start-playback-button {
    opacity: 0;
    pointer-events: none;
    width: 5px;
}
