body.index_about
{
    footer.footer
    {
        display:block;
    }
    section.top
    {
        position:relative;
        height:calc(100vh - 110px - 40px - 50px);
        margin:0 40px 60px 40px;
        background:#161616;
        overflow:hidden;

        .picture
        {
            position:absolute;
            top:-20px;
            left:0;
            width:100%;
            height:calc(100% + 20px);
        }
        .text
        {
            position:absolute;
            top:50%;
            left:0;
            width:100%;
            margin:-115px 0 0 0;
            text-align:center;

            .top
            {
                position:relative;
                color:#85785b;
                font-family:'Mohave';
                font-weight:600;
                font-size:32px;
                letter-spacing:0.06em;

                .stroke
                {
                    display:block;
                    width:55px;
                    height:2px;
                    margin:18px auto 35px auto;
                    background:#85785b;
                }
            }
            .bottom
            {
                color:#fff;
                font-size:37px;

                .line-1
                {
                    font-style:italic;
                }
            }
        }
    }
    section.texts
    {
        width:auto;
        margin:0 224px 0 224px;
        color:#060606;

        .basic
        {
            float:left;
            width:calc(100% - 368px);
            margin:0 0 120px 0;

            .title
            {
                margin:0 0 45px 0;
                font-family:'Mohave';
                font-weight:600;
                color:#85785b;
                text-decoration:underline;
                font-size:32px;
                line-height:53px;
            }
            .text
            {
                line-height:25px;
            }
        }
        .approach
        {
            float:right;
            width:184px;

            .title
            {
                margin:0 0 60px 0;
                font-size:32px;

                .inner
                {
                    position:relative;

                    &::after
                    {
                        content:'';
                        display:block;
                        position:absolute;
                        bottom:1px;
                        left:0;
                        width:100%;
                        height:1px;
                        background:#060606;
                    }
                }
            }
            .text
            {
                font-family:'Mensch';
                font-weight:400;
                text-transform:uppercase;
                line-height:30px;
                font-size:12px;
            }
        }
    }
    section.founders
    {
        position:relative;
        height:650px;
        line-height:650px;
        margin:0 40px 0 40px;
        background:#161616;
        clear:both;
        color:#fff;
        overflow:hidden;

        .title
        {
            display:inline-block;
            vertical-align:middle;
            height:420px;
            line-height:420px;
            font-family:'Mohave';
            font-weight:600;
            text-transform:uppercase;
            font-size:32px;
            text-align:left;
            margin:0 184px 0 184px;

            .inner
            {
                position:relative;

                &::after
                {
                    content:'';
                    display:block;
                    position:absolute;
                    bottom:-5px;
                    left:0;
                    width:100%;
                    height:2px;
                    background:#fff;
                }
            }
        }
        .founder
        {
            display:inline-block;
            vertical-align:middle;
            line-height:18px;
            margin:0 135px 0 0;

            .video
            {
                margin:0 0 32px 0;
                width:240px;
                height:320px;

                video
                {
                    width:100%;
                    height:100%;
                }
            }
            .name
            {
                margin:0 0 8px 0;
                font-size:26px;
            }
            .text
            {
                color:#ababab;
                line-height:18px;

                a.underline
                {
                    &::after
                    {
                        background:#ababab;
                    }
                }
            }
        }
    }
    @mixin max-width $breakpoint-xlarge
    {
        section.texts
        {
            margin-right:160px;
            margin-left:160px;

            .basic
            {
                width:calc(100% - 240px);
            }
            .approach
            {
                width:184px;
            }
        }
        section.founders
        {
            .title
            {
                margin-right:120px;
                margin-left:120px;
            }
        }
    }
    @mixin max-width $breakpoint-large
    {
        section.texts
        {
            margin-right:100px;
            margin-left:100px;

            .basic
            {
                width:calc(100% - 240px);
                margin-bottom:80px;

                .title
                {
                    font-size:28px;
                    line-height:48px;
                }
            }
            .approach
            {
                width:184px;

                .title
                {
                    font-size:28px;
                    line-height:48px;
                }
            }
        }
        section.founders
        {
            height:560px;
            line-height:560px;

            .title
            {
                margin-right:60px;
                margin-left:60px;
            }
            .founder
            {
                margin-left:30px;
                margin-right:30px;

                .video
                {
                    width:200px;
                    height:267px;
                }
            }
        }
    }
    @mixin max-width $breakpoint-medium
    {
        section.top
        {
            .text
            {
                margin-top:-107px;

                .top
                {
                    font-size:28px;

                    .stroke
                    {
                        width:44px;
                        height:2px;
                        margin-bottom:20px;
                    }
                }
                .bottom
                {
                    font-size:33px;
                }
            }
        }
        section.texts
        {
            margin-right:40px;
            margin-left:40px;
        }
        section.founders
        {
            padding-top:60px;
            padding-bottom:30px;
            height:auto;
            line-height:52px;
            text-align:center;

            .title
            {
                display:block;
                width:auto;
                height:auto;
                padding-bottom:40px;
                line-height:52px;
                text-align:center;
            }
            .founder
            {
                margin-right:60px;
                margin-bottom:30px;

                .video
                {
                    width:200px;
                    height:267px;
                }
            }
        }
    }
    @media only screen and (max-width:768px)
    {
        section.top
        {
            margin-bottom:40px;
        }
        section.texts
        {
            .basic
            {
                .title
                {
                    margin-bottom:25px;
                    font-size:25px;
                    line-height:42px;
                }
            }
            .approach
            {
                .title
                {
                    margin-bottom:40px;
                    font-size:25px;
                    line-height:42px;
                }
                .text
                {
                    line-height:26px;
                }
            }
        }
        section.founders
        {
            .founder
            {
                margin-right:15px;
                margin-left:15px;

                .video
                {
                    margin-bottom:20px;
                }
            }
        }
    }
    @mixin max-width $breakpoint-xsmall
    {
        section.top
        {
            .text
            {
                margin-top:-86px;

                .top
                {
                    font-size:24px;

                    .stroke
                    {
                        margin-top:15px;
                        margin-bottom:15px;
                    }
                }
                .bottom
                {
                    font-size:30px;
                }
            }
        }
        section.texts
        {
            .basic
            {
                width:auto;
                margin-bottom:30px;
                float:none;

                .title
                {
                    font-size:22px;
                    line-height:36px;
                }
            }
            .approach
            {
                width:auto;
                margin-bottom:60px;
                float:none;

                .title
                {
                    margin-bottom:25px;
                    font-size:22px;
                    line-height:36px;
                }
            }
        }
    }
    @mixin max-width $breakpoint-xxsmall
    {
        section.top
        {
            margin-right:20px;
            margin-left:20px;

            .text
            {
                margin-top:-62px;

                .top
                {
                    font-size:18px;

                    .stroke
                    {
                        margin-top:10px;
                        margin-bottom:10px;
                    }
                }
                .bottom
                {
                    font-size:22px;
                }
            }
        }
        section.texts
        {
            margin-left:20px;
            margin-right:20px;

            .basic
            {
                .title
                {
                    font-size:20px;
                    line-height:32px;
                }
            }
            .approach
            {
                .title
                {
                    font-size:20px;
                    line-height:32px;
                }
            }
        }
        section.founders
        {
            margin-left:20px;
            margin-right:20px;
            padding-top:40px;

            .title
            {
                margin-left:0;
                margin-right:0;
                font-size:24px;
            }
        }
    }
    @media only screen and (orientation:portrait)
    {
        section.top
        {
            height:calc(100vw * 765 / 1840 + 20px);
        }
    }
}
