section.vertical-split
{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    overflow:hidden;

    .visuals
    {
        display:block;
        position:absolute;
        top:90px;
        right:20px;
        bottom:calc(50% - 45px);
        left:20px;
        background:#cfd2d2;
        z-index:0;

        .picture
        {
            display:block;
            position:absolute;
            top:0;
            left:0;
            width:100%;
            height:100%;
        }
    }
    .texts
    {
        display:flex;
        position:absolute;
        top:calc(50% + 45px);
        right:20px;
        bottom:40px;
        left:60px;
        color:#000;

        flex-direction:column;
        justify-content:center;
        align-items:center;

        .sur-title
        {
            margin:0 0 7px 0;
            font-size:19px;
            color:#949494;

            .arrow
            {
                display:inline-block;
                width:10px;
                height:7px;
                margin:0 0 0 30px;

                svg
                {
                    display:block;
                    width:100%;
                    height:100%;
                }
            }
        }
        .title-container
        {
            display:block;
            text-decoration:none;

            .title
            {
                display:block;
                font-size:45px;
                font-family:'Mohave';
                font-weight:600;
                font-style:normal;
            }
            .date
            {
                position:absolute;
                top:22px;
                right:calc(100% + 17px);

                .stroke
                {
                    display:block;
                    position:absolute;
                    top:0;
                    right:0;
                    width:40px;
                    height:1px;
                    background:#000;
                    transform:scaleX(1);
                }
                .top
                {
                    position:absolute;
                    right:0;
                    bottom:5px;
                }
                .bottom
                {
                    position:absolute;
                    top:5px;
                    right:0;
                }
            }
        }
        .descriptions-container
        {
            position:relative;
            padding:13px 0 0 0;
            line-height:12px;
            font-family:'Mensch';
            font-weight:400;
            font-size:10px;
            letter-spacing:1px;

            .stroke
            {
                display:block;
                position:absolute;
                top:0;
                left:0;
                width:25px;
                height:1px;
                background:#000;
            }
        }
    }
    .view
    {
        position:absolute;
        bottom:14px;
        left:50%;
        width:150px;
        margin-left:-75px;
        text-align:center;
        text-decoration:none;
        font-size:13px;

        .arrow
        {
            display:inline-block;
            width:7px;
            height:10px;
            transform:rotate(90deg);

            svg
            {
                display:block;
                width:100%;
                height:100%;
            }
        }
    }
    @media only screen and (orientation:landscape)
    {
        .visuals
        {
            top:90px;
            right:20px;
            bottom:20px;
            left:50%;
        }
        .texts
        {
            top:90px;
            right:50%;
            bottom:50px;
            left:calc(33% / 2 + 20px);
        }
        .view
        {
            bottom:20px;
            left:25%;
        }
    }
    @mixin max-height $breakpoint-xxsmall
    {
        .visuals
        {
            top:70px;
        }
        .texts
        {
            top:60px;
        }
    }
}
