body.works_single
{
    footer.footer
    {
        display:block;
    }
    section.top
    {
        position:relative;
        height:calc(100vh - 110px - 220px);
        margin:0 40px 60px 40px;
        overflow:hidden;

        .picture
        {
            position:absolute;
            top:-20px;
            left:0;
            width:100%;
            height:calc(100% + 20px);
        }
    }
    section.columns
    {
        width:925px;
        margin:0 auto 38px auto;
        color:#000;

        /* clearfix */
        &::after
        {
            content:'';
            display:table;
            clear:both;
        }
        .launch
        {
            position:relative;
            display:inline-block;
            padding:0 0 4px 0;
            color:#878787;

            &.underline
            {
                &::after
                {
                    background:#a9a9a9;
                }
            }
        }
        .column
        {
            position:relative;
            float:left;
            vertical-align:top;

            &.text
            {
                width:180px;
                line-height:20px;
                font-size:14px;

                .label
                {
                    margin:0 0 14px 0;
                    font-family:'Mensch';
                    font-weight:400;
                    color:#9b9b9b;
                }
                .logo
                {
                    margin-bottom:15px;

                    svg *
                    {
                        fill:#3c3c3c;
                    }
                    &.awwwards
                    {
                        width:130px;
                        height:15px;
                    }
                    &.fd
                    {
                        width:52px;
                        height:21px;
                    }
                    &.fwa
                    {
                        width:72px;
                        height:22px;
                    }
                    &.adobe
                    {
                        width:38px;
                        height:43px;
                    }
                    &.design-licks
                    {
                        width:89px;
                        height:16px;
                    }
                    &.css-design-awards
                    {
                        width:34px;
                        height:49px;
                    }
                }
                .launch
                {
                    margin-top:38px;
                }
            }
            &.title
            {
                width:385px;
                margin-top:-3px;

                .title
                {
                    display:block;
                    max-width:320px;
                    line-height:65px;
                    font-family:'Mohave';
                    font-weight:600;
                    font-size:60px;
                    margin:0 0 18px 0;
                }
                .sub-title
                {
                    display:block;
                    font-family:'Mensch';
                    font-weight:400;
                    font-size:14px;

                    .with
                    {
                        display:inline-block;
                        margin-right:10px;
                        color:#9b9b9b;
                    }
                }
                .date
                {
                    display:block;
                    position:absolute;
                    top:24px;
                    right:calc(100% + 28px);

                    .stroke
                    {
                        display:block;
                        position:absolute;
                        top:0;
                        right:0;
                        width:40px;
                        height:1px;
                        background:#000;
                    }
                    .top
                    {
                        position:absolute;
                        right:0;
                        bottom:5px;
                    }
                    .bottom
                    {
                        position:absolute;
                        top:5px;
                        right:0;
                    }
                }
                .launch
                {
                    margin-top:28px;
                }
            }
            &.website
            {
                .launch
                {
                    margin-top:0;
                }
            }
        }
    }
    section.content
    {
        margin:0 0 155px 0;

        .line
        {
            position:relative;
            margin-bottom:80px;

            .picture,
            .video
            {
                position:absolute;
                top:0;
                left:0;
                width:100%;
                height:100%;
            }
            .video
            {
                video
                {
                    position:absolute;
                    top:0;
                    left:0;
                    width:100%;
                    height:100%;
                }
                .play-container
                {
                    display:none;
                    position:absolute;
                    top:0;
                    left:0;
                    width:100%;
                    height:100%;
                    background:rgba(0,0,0,0.6);
                    transition:opacity 0.3s;
                    will-change:opacity;
                    pointer-events:none;

                    .icon
                    {
                        position:absolute;
                        top:50%;
                        left:50%;
                        width:22px;
                        height:22px;
                        margin:-11px 0 0 -11px;

                        svg
                        {
                            display:block;
                            width:100%;
                            height:100%;

                            *
                            {
                                fill:#fff;
                            }
                        }
                    }
                }
                &.play
                {
                    .play-container
                    {
                        opacity:0;
                    }
                }
            }
            .picture-container
            {
                position:relative;
            }
            &.text
            {
                width:925px;
                padding:20px 0 0 0;
                margin-left:auto;
                margin-right:auto;
                clear:both;
                line-height:26px;

                p
                {
                    margin:0 0 1.5em 0;
                }
            }
            &.picture-full
            {
                .picture-container
                {
                    margin-left:auto;
                    margin-right:auto;

                    &.big
                    {
                        width:1472px;
                        height:784px;
                    }
                    &.medium
                    {
                        width:1104px;
                        height:622px;
                    }
                }
            }
            &.pictures-2
            {
                text-align:center;
                font-size:0;
                line-height:0;

                .picture-container
                {
                    display:inline-block;

                    &.landscape
                    {
                        width:690px;
                        height:560px;
                    }
                    &.portrait
                    {
                        width:460px;
                        height:750px;
                    }
                    &.square
                    {
                        width:690px;
                        height:690px;
                    }
                    &.index-1
                    {
                        margin-right:40px;
                    }
                    &.index-2
                    {
                        margin-left:40px;
                    }
                }
            }
            &.pictures-4
            {
                text-align:center;
                font-size:0;
                line-height:0;

                .left
                {
                    display:inline-block;
                    margin-right:40px;
                    width:690px;
                    text-align:right;
                }
                .right
                {
                    display:inline-block;
                    margin-left:40px;
                    width:690px;
                    text-align:left;
                }
                .picture-container
                {
                    display:inline-block;

                    &.landscape
                    {
                        width:690px;
                        height:560px;
                    }
                    &.portrait
                    {
                        width:460px;
                        height:750px;
                    }
                    &:nth-child(1)
                    {
                        margin-bottom:40px;
                    }
                }
            }
            &.model
            {
                text-align:center;

                .shape-grid
                {
                    display:inline-block;
                    position:relative;
                    background:#000;

                    .center
                    {
                        position:relative;
                        background:#161616;
                        overflow:hidden;
                    }
                }
                &.desktop
                {
                    .shape-grid
                    {
                        border-radius:24px;

                        .center
                        {
                            width:865px;
                            margin:35px;
                        }
                    }
                }
                &.tablet-portrait
                {
                    .shape-grid
                    {
                        border-radius:34px;

                        .center
                        {
                            width:398px;
                            margin:54px;
                        }
                    }
                }
                &.smartphone-portrait
                {
                    .shape-grid
                    {
                        border-radius:36px;

                        .center
                        {
                            width:220px;
                            margin:60px 14px;
                        }
                    }
                }
                &.tablet-landscape
                {
                    .shape-grid
                    {
                        border-radius:34px;

                        .center
                        {
                            width:636px;
                            margin:54px;
                        }
                    }
                }
                &.smartphone-landscape
                {
                    .shape-grid
                    {
                        border-radius:36px;

                        .center
                        {
                            width:390px;
                            margin:14px 60px;
                        }
                    }
                }
            }
            &.carousel
            {
                background:#161616;
                height:100vh;
                overflow:hidden;

                .content
                {
                    position:absolute;
                    top:105px;
                    bottom:110px;
                    width:100%;
                    overflow:hidden;

                    .items
                    {
                        position:absolute;
                        top:0;
                        left:50%;
                        width:9999999px;
                        height:100%;
                        font-size:0;
                        transition:transform cubic-bezier(0.85, 0, 0.15, 1);
                        will-change:transform;

                        .item
                        {
                            position:relative;
                            display:inline-block;
                            background:#bfbfbf;
                            height:100%;
                            margin:0 90px;
                            cursor:pointer;
                            vertical-align:middle;

                            .picture
                            {
                                position:absolute;
                                top:0;
                                left:0;
                                width:100%;
                                height:100%;

                                img
                                {
                                    width:100%;
                                    height:100%;
                                }
                                .transp
                                {
                                    display:block;
                                    position:absolute;
                                    top:0;
                                    left:0;
                                    width:100%;
                                    height:100%;
                                    background:url('../images/transp.png');
                                }
                            }
                            &.active
                            {
                                cursor:initial;

                                .picture
                                {
                                    .transp
                                    {
                                        display:none;
                                    }
                                }
                            }
                        }
                        &.animated
                        {
                            transition-duration:0.6s;
                        }
                    }
                }
                .pagination
                {
                    position:absolute;
                    bottom:35px;
                    left:0;
                    width:100%;
                    height:40px;

                    .items
                    {
                        position:absolute;
                        top:0;
                        left:0;
                        width:100%;
                        height:100%;
                        text-align:center;
                        font-size:0;

                        .item
                        {
                            position:relative;
                            display:inline-block;
                            width:40px;
                            height:100%;

                            .dot
                            {
                                position:absolute;
                                top:50%;
                                left:50%;
                                width:8px;
                                height:8px;
                                margin:-4px 0 0 -4px;
                                background:#737373;
                                border-radius:4px;
                                transition:background 0.15s;
                                will-change:background;
                            }
                            &.active
                            {
                                .dot
                                {
                                    background:#fff;
                                }
                            }
                            @mixin no-touch
                            {
                                &:hover
                                {
                                    .dot
                                    {
                                        background:#fff;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    section.siblings
    {
        width:520px;
        margin:0 auto 65px auto;
        text-align:center;
        font-size:0;

        a
        {
            display:inline-block;
            vertical-align:top;
            width:50%;
            text-decoration:none;

            .label
            {
                display:block;
                margin:0 0 14px 0;
                font-family:'Mensch';
                font-weight:400;
                font-size:14px;
                color:#9b9b9b;

                .arrow
                {
                    display:inline-block;
                    width:20px;
                    height:13px;
                    margin:0 8px -1px 8px;
                    transition:transform 0.3s;
                    will-change:transform;

                    svg
                    {
                        display:block;
                        width:100%;
                        height:100%;

                        *
                        {
                            fill:#9b9b9b;
                        }
                    }
                }
            }
            .title
            {
                display:block;
                line-height:40px;
                font-family:'Mohave';
                font-weight:600;
                font-size:37px;
                color:#000;
            }
            &.previous
            {
                padding:0 45px 0 0;
                text-align:right;


                .label
                {
                    .arrow
                    {
                        transform:rotate(180deg);
                    }
                }
            }
            &.next
            {
                padding:0 0 0 45px;
                text-align:left;
            }
            @mixin no-touch
            {
                &:hover
                {
                    .label
                    {
                        .arrow
                        {
                            transform:translateX(6px);
                        }
                    }
                    &.previous
                    {
                        .label
                        {
                            .arrow
                            {
                                transform:rotate(180deg) translateX(6px);
                            }
                        }
                    }
                }
            }
        }
    }
