                            {
                                width:208px;
                            }
                        }
                    }
                    &.smartphone-portrait
                    {
                        .shape-grid
                        {
                            border-radius:32px;

                            .center
                            {
                                width:169px;
                                margin:50px 12px;
                            }
                        }
                    }
                    &.tablet-landscape
                    {
                        .shape-grid
                        {
                            border-radius:20px;

                            .center
                            {
                                width:332px;
                            }
                        }
                    }
                    &.smartphone-landscape
                    {
                        .shape-grid
                        {
                            border-radius:32px;

                            .center
                            {
                                width:300px;
                                margin:12px 50px;
                            }
                        }
                    }
                }
            }
        }
        section.siblings
        {
            width:auto;

            a
            {
                &.previous
                {
                    padding-right:25px;
                }
                &.next
                {
                    padding-left:25px;
                }
            }
        }
    }
    @mixin max-width $breakpoint-xxsmall
    {
        section.top
        {
            margin-left:20px;
            margin-bottom:20px;
            margin-right:20px;
        }
        section.columns
        {
            margin-left:20px;
            margin-right:20px;

            .column
            {
                .label
                {
                    margin-bottom:5px;
                }
                &.title
                {
                    margin-left:42px;

                    .date
                    {
                        top:20px;
                        right:calc(100% + 12px);

                        .top
                        {
                            bottom:2px;
                        }
                        .bottom
                        {
                            top:2px;
                        }
                        .stroke
                        {
                            width:30px;
                        }
                    }
                    .title
                    {
                        /*padding-left:26px;*/
                        font-size:38px;
                        line-height:42px;
                    }
                    .sub-title
                    {
                        font-size:12px;
                    }
                    .launch
                    {
                        margin-top:10px;
                    }
                }
            }
        }
        section.content
        {
            margin-bottom:65px;

            .line
            {
                &.text
                {
                    margin-left:20px;
                    margin-right:20px;
                }
                &.picture-full
                {
                    .picture-container
                    {
                        &.big
                        {
                            width:280px;
                            height:149px;
                        }
                        &.medium
                        {
                            width:280px;
                            height:149px;
                        }
                    }
                }
                &.pictures-2
                {
                    .picture-container
                    {
                        &.landscape
                        {
                            width:280px;
                            height:227px;
                        }
                        &.portrait
                        {
                            width:280px;
                            height:457px;
                        }
                        &.square
                        {
                            width:280px;
                            height:280px;
                        }
                    }
                }
                &.pictures-4
                {
                    .left
                    {
                        width:280px;
                    }
                    .right
                    {
                        width:280px;
                    }
                    .picture-container
                    {
                        &.landscape
                        {
                            width:280px;
                            height:227px;
                        }
                        &.portrait
                        {
                            width:280px;
                            height:457px;
                        }
                    }
                }
                &.model
                {
                    .shape-grid
                    {
                        background:none;
                    }
                    &.desktop
                    {
                        .shape-grid
                        {
                            .center
                            {
                                width:280px;
                                margin:0;
                            }
                        }
                    }
                    &.tablet-portrait
                    {
                        .shape-grid
                        {
                            .center
                            {
                                width:280px;
                                margin:0;
                            }
                        }
                    }
                    &.smartphone-portrait
                    {
                        .shape-grid
                        {
                            .center
                            {
                                width:280px;
                                margin:0;
                            }
                        }
                    }
                    &.tablet-landscape
                    {
                        .shape-grid
                        {
                            .center
                            {
                                width:280px;
                                margin:0;
                            }
                        }
                    }
                    &.smartphone-landscape
                    {
                        .shape-grid
                        {
                            .center
                            {
                                width:280px;
                                margin:0;
                            }
                        }
                    }
                }
            }
        }
        section.siblings
        {
            margin-bottom:45px;

            a
            {
                .title
                {
                    font-size:32px;
                    line-height:36px;
                }
                &.previous
                {
                    padding-right:15px;
                    padding-left:10px;
                }
                &.next
                {
                    padding-right:10px;
                    padding-left:15px;
                }
            }
        }
    }
    @mixin max-height $breakpoint-xxsmall
    {
        section.top
        {
            height:calc(100vh - 110px - 30px);
            margin-bottom:40px;
        }
    }
    @media only screen and (orientation:portrait)
    {
        section.top
        {
            height:calc(100vw * 765 / 1840);
        }
        section.content
        {
            .line
            {
                &.carousel
                {
                    background:#161616;
                    height:100vw;
                }
            }
        }
    }
}

html.no-video
{
    body.works_single
    {
        section.content
        {
            .line
            {
                &.model
                {
                    &.has-video:not(.has-picture)
                    {
                        display:none;
                    }
                }
            }
        }
    }
}
html.no-videoautoplay
{
    body.works_single
    {
        section.content
        {
            .line
            {
                .video
                {
                    .play-container
                    {
                        display:block;
                    }
                }
            }
        }
    }
}
